/* css reste */
* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

/* style */
body {
  background: #ffffff;
  overflow: hidden;
}

nav {
  min-width: 100vw;
  height: 50px;
  display: flex;
  justify-content: space-between;
}
nav > h1{
  font-family: 'Segoe Print';
  line-height: 50px;
  font-size: 28px;
  padding-left: 30px;
}
nav > .sign {
  padding-right: 30px;
  line-height: 50px;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
}
nav > .sign a:hover {
  color: #0282b9;
  cursor: pointer;
}
nav > .sign > .signLogo {
  margin-right: 10px;
  width: 20px;
  height: 20px;
}
nav > .sign > .signLogo > .icon{
  width: 20px;
  height: 20px;
}


.globalLogo {
  width: 300px;
  margin: 0 auto;
}
@media (min-width: 500px) {
  .globalLogo {
    width: 500px;
  }
}
@keyframes logoRotate {
    0%{
      transform: rotateY(0deg)
    }
    50% {
      transform: rotateY(180deg)
    }
    100% {
      transform: rotateY(360deg)
    }
}   

.globalLogo:hover {
  animation: logoRotate 2s linear;
}



.globalHeader {
  margin: 0 20px 0 20px;
  display: flex;
  justify-content: space-between;
  background-clip: padding-box,
  border-box;
  background-origin:padding-box,
  border-box;
  background-image:linear-gradient(180deg, #000, #000),
  linear-gradient(180deg, rgb(216, 46, 118), rgb(8, 105, 175));
  border: 6px transparent solid;
  border-radius: 3px;
  position: relative;
}
@media (min-width: 500px) {
  .globalHeader {
    margin: 20px auto 60px auto;
    max-width: 700px;
  }
}
.globalHeader > .choiceBtn {
  height: 40px;
  background-color: #fff;
}
.globalHeader > .choiceBtn > img {
  margin-top: 7px;
}

.globalHeader > .bigBox {
  position: absolute;
  width: 80px;
  top: 50px;
  z-index: 1;
  display: none;
}
@media (min-width: 500px) {
  .globalHeader>.bigBox {
    width: 95px;
  }
}

.globalHeader > .searchForm {
  width: 100%;
  display: flex;
}
.globalHeader > .searchForm > input {
  width: 100%;
  height: 40px;
  padding: 0 10px;
  font-size: 16px;
  border: none;
  outline: none;
}
.globalHeader >.searchForm > button {
  white-space: nowrap;
  border: none;
  background-color: #fff;
  width: 50px;
  outline: none;
}
.globalHeader >.searchForm > button >.icon {
  width: 30px;
  height: 30px;
}



.globalMain {
  max-width: 900px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
@keyframes out {
  from{
    transform: translateY(0);
  }to{
    transform: translateY(500%);
  }
}
@keyframes in {
  from{
    transform: translateY(500%);
  }to{
    transform: translateY(0);
  }
}
.globalMain.out {
  animation: out 0.5s linear forwards;
}
.globalMain.in{
  animation: in 0.5s linear forwards;
}

.siteList {
  margin: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media (min-width: 500px) {
  .siteList {
    margin-left: 0;
    margin-right: -25px;
    justify-content: flex-start;
  }
}

.siteList > li {
  margin-bottom: 10px;
  position: relative;
  cursor: pointer;
}
@media (min-width: 500px) {
  .siteList > li {
    margin-right: 25px;
  }
}

.siteList .site {
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-clip: padding-box,
  border-box;
  background-origin:padding-box,
  border-box;
  background-image:linear-gradient(180deg, #fff, #fff),
  linear-gradient(180deg, rgb(216, 46, 118), rgb(8, 105, 175));
  border: 6px transparent solid;
  border-radius: 3px;
  padding: 20px 0;
  font-family: 'Segoe Print';
}

.siteList .site > .logo {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
  text-transform: uppercase;
}

.siteList .site > .link {
  margin-top: 4px;
  font-size: 14px;
}

.siteList .close {
  position: absolute;
  top: 4px;
  right: 4px;
  display: none;
}

.siteList li:hover > .close {
  display: block;
  cursor: default;
}

.siteList .addButton {
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-clip: padding-box,
  border-box;
  background-origin:padding-box,
  border-box;
  background-image:linear-gradient(180deg, #fff, #fff),
  linear-gradient(180deg, rgb(216, 46, 118), rgb(8, 105, 175));
  border: 6px transparent solid;
  border-radius: 3px;
  padding: 20px 0;
  font-family: 'Segoe Print';
}

.siteList .addButton .icon {
  width: 56px;
  height: 56px;
}

.siteList .addButton .iconWrapper {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.siteList .addButton .text {
  margin-top: 4px;
  font-size: 14px;
}
